/** animation**/
#screen_2 {
  animation: screen2  1s linear alternate;
  transform-origin: center;
  transform-box: fill-box;
  animation-delay: 0.5s; }

@keyframes screen2 {
  from {
    transform: scale(0); }
  to {
    transform: scale(1); } }

#screen_1 {
  animation: screen1  1s linear alternate;
  transform-origin: center;
  transform-box: fill-box;
  animation-delay: 1s; }

@keyframes screen1 {
  from {
    transform: scale(0); }
  to {
    transform: scale(1); } }

#dollar {
  animation: dollar  1.5s linear alternate infinite;
  transform-origin: center;
  transform-box: fill-box; }

@keyframes dollar {
  from {
    transform: rotateY(0); }
  to {
    transform: rotateY(30deg); } }

